import React, {FC, HTMLAttributes} from 'react'
import styles from "./FighterItem.module.scss";
import classnames from 'classnames'
import {makeImageFighterUrl, makeImageIconUrl} from '../../../utils/utils'
import {Card} from '../../../store/gameReducer'

type Props = {
  item: Card
  isFocused: boolean
} & HTMLAttributes<HTMLDivElement>

const FighterItem:FC<Props> = ({item, className = "", isFocused, ...restProps}) => {
  const {iconName, name, healthPoints, attack, defend, dodge, move} = item;
  const isDead = healthPoints === 0;

  return (
    <div
      className={classnames(styles.root, className, {
        [styles.focused]: isFocused
      })}
      {...restProps}
    >
      <div className={styles.imageContainer}>
        <img className={styles.imageFighter} src={makeImageFighterUrl(iconName)} alt={name} />
        {isDead && <img className={styles.iconOnItem} src={makeImageIconUrl("ic_fighter_is_dead")} alt="Is dead"/>}
        {attack.applied && move.applied && <img className={styles.iconOnItem} src={makeImageIconUrl("ic_condition_out_of_actions")} alt="Is dead"/>}
        {defend.applied && <img className={styles.iconOnItem} src={makeImageIconUrl("ic_action_defense")} alt="Is dead"/>}
        {dodge.applied && <img className={styles.iconOnItem} src={makeImageIconUrl("ic_action_defense")} alt="Is dead"/>}
        {move.applied && !attack.applied && <img className={styles.iconOnItem} src={makeImageIconUrl("ic_moves")} alt="Is dead"/>}
      </div>
    </div>
  )
};

export default FighterItem;